
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Loading from 'vux/src/components/loading/index.vue'
import Card from 'vux/src/components/card/index.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import GroupTitle from 'vux/src/components/group-title/index.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import Panel from 'vux/src/components/panel/index.vue'
import Grid from 'vux/src/components/grid/grid.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'
import Icon from 'vux/src/components/icon/index.vue'

import {mapState, mapGetters} from 'vuex'
import img1 from '../assets/img/7.jpg'
import img2 from '../assets/img/8.jpg'
import img3 from '../assets/img/9.jpg'

const baseList = [{
img: img3,
title: ''
}, {
img: img2,
title: ''
}, {
img: img1,
title: '>>'
}]
export default {
components: {
TransferDom, Loading, Card, Swiper, GroupTitle, SwiperItem, Panel, Grid, GridItem, Icon
},
created() {
this.$store.commit('updateUserCode', 20211);
},
data() {
return {
demo01_list: baseList,
demo01_index: 0
}
},
directives: {
TransferDom
},
computed: {
...mapState({}),
...mapGetters({})
},
methods: {
swiperClick(index) {
if (index === 2) {
window.location.href="../../phone/measure.png"
}
}
}
}



